import React from 'react';
import { useApp } from '../context/AppContext';
import './Settings.css';

const Settings: React.FC = () => {
  const { state, updateSettings } = useApp();

  const handleFontSizeChange = (size: 'small' | 'medium' | 'large') => {
    updateSettings({ fontSize: size });
  };

  const handleHighContrastToggle = () => {
    updateSettings({ highContrast: !state.settings.highContrast });
  };

  const handleLanguageChange = (language: string) => {
    updateSettings({ language });
  };

  return (
    <div className="settings">
      <div className="settings-header">
        <h1></h1>
        <p></p>
      </div>

      <div className="settings-content">
        {/*  */}
        <section className="setting-section">
          <h2></h2>
          <p className="setting-description"></p>
          <div className="setting-options">
            <button
              onClick={() => handleFontSizeChange('small')}
              className={`option-btn ${state.settings.fontSize === 'small' ? 'active' : ''}`}
            >
              <div className="option-icon">A</div>
              <div className="option-label"></div>
            </button>
            <button
              onClick={() => handleFontSizeChange('medium')}
              className={`option-btn ${state.settings.fontSize === 'medium' ? 'active' : ''}`}
            >
              <div className="option-icon">A</div>
              <div className="option-label"></div>
            </button>
            <button
              onClick={() => handleFontSizeChange('large')}
              className={`option-btn ${state.settings.fontSize === 'large' ? 'active' : ''}`}
            >
              <div className="option-icon">A</div>
              <div className="option-label"></div>
            </button>
          </div>
        </section>

        {/*  */}
        <section className="setting-section">
          <h2></h2>
          <p className="setting-description"></p>
          <div className="setting-toggle">
            <label className="toggle-switch">
              <input
                type="checkbox"
                checked={state.settings.highContrast}
                onChange={handleHighContrastToggle}
                aria-label=""
              />
              <span className="toggle-slider"></span>
            </label>
            <span className="toggle-label">
              {state.settings.highContrast ? '' : ''}
            </span>
          </div>
        </section>

        {/*  */}
        <section className="setting-section">
          <h2></h2>
          <p className="setting-description"></p>
          <div className="setting-select">
            <select
              value={state.settings.language}
              onChange={(e) => handleLanguageChange(e.target.value)}
              aria-label=""
            >
              <option value="zh-TW"></option>
              <option value="zh-CN"></option>
              <option value="en-US">English</option>
              <option value="ja-JP"></option>
            </select>
          </div>
        </section>

        {/*  */}
        <section className="setting-section">
          <h2></h2>
          <p className="setting-description"></p>
          <div className="setting-actions">
            <button className="action-btn export-btn">
              <div className="action-icon"></div>
              <div className="action-content">
                <div className="action-title"></div>
                <div className="action-description"></div>
              </div>
            </button>
            
            <button className="action-btn import-btn">
              <div className="action-icon"></div>
              <div className="action-content">
                <div className="action-title"></div>
                <div className="action-description"></div>
              </div>
            </button>
            
            <button className="action-btn clear-btn">
              <div className="action-icon"></div>
              <div className="action-content">
                <div className="action-title"></div>
                <div className="action-description"></div>
              </div>
            </button>
          </div>
        </section>

        {/*  */}
        <section className="setting-section">
          <h2></h2>
          <div className="about-info">
            <div className="app-info">
              <div className="app-icon"></div>
              <div className="app-details">
                <div className="app-name"></div>
                <div className="app-version"> 1.0.0</div>
              </div>
            </div>
            
            <div className="about-links">
              <button className="about-link" onClick={() => alert('')}></button>
              <button className="about-link" onClick={() => alert('')}></button>
              <button className="about-link" onClick={() => alert('')}></button>
              <button className="about-link" onClick={() => alert('')}></button>
            </div>
          </div>
        </section>
      </div>
    </div>
  );
};

export default Settings;

