import { WebhookRequest, WebhookResponse, ChatMessage } from '../types';

const WEBHOOK_URL = 'http://163.25.107.224:5678/webhook/d6fdcf14-6199-4401-b127-c90afcef4543';

// 
// eslint-disable-next-line @typescript-eslint/no-unused-vars
async function retryWithBackoff<T>(
  fn: () => Promise<T>,
  maxRetries: number = 2,
  baseDelay: number = 1000
): Promise<T> {
  let lastError: Error | null = null;
  
  for (let attempt = 0; attempt <= maxRetries; attempt++) {
    try {
      return await fn();
    } catch (error) {
      lastError = error as Error;
      
      if (attempt === maxRetries) {
        throw lastError;
      }
      
      //  4xx 
      if (error instanceof Error && error.message.includes('4')) {
        throw lastError;
      }
      
      // 
      const delay = baseDelay * Math.pow(2, attempt);
      await new Promise(resolve => setTimeout(resolve, delay));
    }
  }
  
  throw lastError || new Error('');
}

//  webhook 
export async function sendWebhookMessage(request: WebhookRequest): Promise<WebhookResponse> {
  try {
    //  Date  ISO 
    const requestData = {
      messages: request.messages.map(msg => ({
        ...msg,
        timestamp: msg.timestamp instanceof Date ? msg.timestamp.toISOString() : msg.timestamp
      })),
      metadata: request.metadata
    };

    // 
    console.log(' webhook :', {
      url: WEBHOOK_URL,
      method: 'POST',
      request: requestData
    });

    //  POST 
    const response = await fetch(WEBHOOK_URL, {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(requestData),
    });

    console.log('Webhook :', response.status, response.statusText);

    if (!response.ok) {
      const errorText = await response.text();
      console.error('Webhook :', errorText);
      throw new Error(`HTTP ${response.status}: ${errorText}`);
    }

    const data = await response.json();
    console.log('Webhook :', data);
    console.log(':', typeof data);
    console.log(':', Object.keys(data));
    
    // 
    if (!data) {
      console.error('');
      throw new Error('');
    }
    
    if (typeof data !== 'object') {
      console.error(':', typeof data, data);
      throw new Error('');
    }
    
    // 
    let content: string | undefined;
    let role: 'user' | 'assistant' | 'system' = 'assistant';
    let id: string = 'webhook-' + Date.now();
    let timestamp: Date = new Date();
    
    //  reply 
    if (data.reply && typeof data.reply === 'object' && data.reply.content) {
      console.log(' reply ');
      content = data.reply.content;
      role = data.reply.role || 'assistant';
      id = data.reply.id || 'webhook-' + Date.now();
      timestamp = data.reply.timestamp ? new Date(data.reply.timestamp) : new Date();
    }
    //  output 
    else if (data.output) {
      if (typeof data.output === 'string') {
        console.log(' output ');
        content = data.output;
      } else if (typeof data.output === 'object' && data.output.content) {
        console.log(' output ');
        content = data.output.content;
        role = data.output.role || 'assistant';
        id = data.output.id || 'webhook-' + Date.now();
        timestamp = data.output.timestamp ? new Date(data.output.timestamp) : new Date();
      }
    }
    // 
    else if (data.content) {
      console.log(' content ');
      content = data.content;
    } else if (data.message) {
      console.log(' message ');
      content = data.message;
    }
    
    if (!content) {
      console.error(':', Object.keys(data));
      console.error(':', JSON.stringify(data, null, 2));
      throw new Error(' webhook ');
    }
    
    //  content 
    if (typeof content !== 'string') {
      console.error('content :', typeof content, content);
      throw new Error('content ');
    }

    // 
    const reply: ChatMessage = {
      id,
      role,
      content,
      timestamp,
    };

    console.log(':', reply);

    return {
      reply,
      conversation_id: data.conversation_id || data.conversationId || 'webhook-' + Date.now(),
      error: data.error || null,
    } as WebhookResponse;
  } catch (error) {
    console.error('Webhook :', error);
    console.error(':', error instanceof Error ? error.stack : '');
    throw error;
  }
}

// SSE 
export function useEventSource(
  url: string,
  onMessage: (data: any) => void,
  onError?: (error: Event) => void
) {
  const eventSource = new EventSource(url);
  
  eventSource.onmessage = (event) => {
    try {
      const data = JSON.parse(event.data);
      onMessage(data);
    } catch (error) {
      console.error('SSE :', error);
    }
  };
  
  eventSource.onerror = (error) => {
    console.error('SSE :', error);
    onError?.(error);
  };
  
  return {
    close: () => eventSource.close(),
  };
}

