import React from 'react';
import { useLocation, Link } from 'react-router-dom';
import { NavItem } from '../types';
import { useAuth } from '../context/AuthContext';
import './Navigation.css';

const navItems: NavItem[] = [
  { id: 'dashboard', label: '', icon: '', path: '/' },
  { id: 'ai-assistant', label: 'AI ', icon: '', path: '/ai-assistant' },
  { id: 'health-records', label: '', icon: '', path: '/health-records' },
  { id: 'calendar', label: '', icon: '', path: '/calendar' },
  { id: 'emergency', label: '', icon: '', path: '/emergency' },
  { id: 'settings', label: '', icon: '', path: '/settings' }
];

const Navigation: React.FC = () => {
  const location = useLocation();
  const { state: authState, logout } = useAuth();

  return (
    <>
      {/*  */}
      <nav className="navbar desktop-nav" role="navigation" aria-label="">
        <div className="navbar-container">
          <div className="navbar-brand">
            <h1 className="navbar-title"></h1>
            {authState.user && (
              <div className="user-info">
                <span className="user-name">{authState.user.full_name || authState.user.username}</span>
                <button 
                  onClick={logout}
                  className="logout-btn"
                  aria-label=""
                >
                  
                </button>
              </div>
            )}
          </div>
          
          <ul className="navbar-menu">
            {navItems.map((item) => (
              <li key={item.id} className="navbar-item">
                <Link
                  to={item.path}
                  className={`navbar-link ${location.pathname === item.path ? 'active' : ''}`}
                  aria-current={location.pathname === item.path ? 'page' : undefined}
                >
                  <span className="navbar-icon" aria-hidden="true">{item.icon}</span>
                  <span className="navbar-label">{item.label}</span>
                </Link>
              </li>
            ))}
          </ul>

          {/*  */}
          <div className="breadcrumb" aria-label="">
            <span className="breadcrumb-item"></span>
            {location.pathname !== '/' && (
              <>
                <span className="breadcrumb-separator">/</span>
                <span className="breadcrumb-item">
                  {navItems.find(item => item.path === location.pathname)?.label}
                </span>
              </>
            )}
          </div>
        </div>
      </nav>

      {/*  */}
      <nav className="tabbar mobile-nav" role="navigation" aria-label="">
        <ul className="tabbar-menu">
          {navItems.map((item) => (
            <li key={item.id} className="tabbar-item">
              <Link
                to={item.path}
                className={`tabbar-link ${location.pathname === item.path ? 'active' : ''}`}
                aria-current={location.pathname === item.path ? 'page' : undefined}
              >
                <span className="tabbar-icon" aria-hidden="true">{item.icon}</span>
                <span className="tabbar-label">{item.label}</span>
              </Link>
            </li>
          ))}
          {/*  */}
          {authState.user && (
            <li className="tabbar-item">
              <button
                onClick={logout}
                className="tabbar-link logout-tabbar-btn"
                aria-label=""
              >
                <span className="tabbar-icon" aria-hidden="true"></span>
                <span className="tabbar-label"></span>
              </button>
            </li>
          )}
        </ul>
      </nav>
    </>
  );
};

export default Navigation;
