import React, { useState, useRef, useEffect } from 'react';
import { useApp } from '../context/AppContext';
import { ChatMessage } from '../types';
import ChatBubble from '../components/ChatBubble';
import ChatInput from '../components/ChatInput';
import { sendWebhookMessage } from '../lib/webhook';
import toast from 'react-hot-toast';
import './AIAssistant.css';

const AIAssistant: React.FC = () => {
  const { state, dispatch, addChatMessage, clearChatHistory } = useApp();
  const [isLoading, setIsLoading] = useState(false);
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  const [isTyping, setIsTyping] = useState(false);
  const messagesEndRef = useRef<HTMLDivElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [state.chatHistory]);

  const handleSendMessage = async (content: string) => {
    if (!content.trim()) return;

    const userMessage: ChatMessage = {
      id: Date.now().toString(),
      role: 'user',
      content: content.trim(),
      timestamp: new Date()
    };

    addChatMessage(userMessage);

    //  AI 
    const aiMessageId = (Date.now() + 1).toString();
    const aiMessage: ChatMessage = {
      id: aiMessageId,
      role: 'assistant',
      content: '',
      timestamp: new Date(),
      isTyping: true
    };

    addChatMessage(aiMessage);
    setIsLoading(true);
    setIsTyping(true);

    try {
      const response = await sendWebhookMessage({
        messages: state.chatHistory.concat(userMessage),
        metadata: {
          client: 'web',
          locale: state.settings.language
        }
      });

      if (response.error) {
        throw new Error(response.error);
      }

      //  dispatch  AI 
      dispatch({
        type: 'UPDATE_CHAT_MESSAGE',
        payload: {
          id: aiMessageId,
          updates: {
            content: response.reply.content,
            isTyping: false,
            timestamp: response.reply.timestamp || new Date()
          }
        }
      });

      //  ID
      if (response.conversation_id) {
        dispatch({
          type: 'SET_CONVERSATION_ID',
          payload: response.conversation_id
        });
      }

    } catch (error) {
      console.error('Webhook error:', error);
      toast.error('');
      
      // 
      const updatedHistory = state.chatHistory.filter(msg => msg.id !== aiMessageId);
      dispatch({
        type: 'CLEAR_CHAT_HISTORY'
      });
      // 
      updatedHistory.forEach(msg => {
        if (msg.id !== aiMessageId) {
          addChatMessage(msg);
        }
      });
    } finally {
      setIsLoading(false);
      setIsTyping(false);
    }
  };

  const handleClearChat = () => {
    if (window.confirm('')) {
      clearChatHistory();
      toast.success('');
    }
  };

  return (
    <div className="ai-assistant">
      <div className="ai-assistant-header">
        <h1>AI </h1>
        <button 
          onClick={handleClearChat}
          className="clear-chat-btn"
          aria-label=""
        >
          
        </button>
      </div>

      <div className="chat-container" role="log" aria-live="polite">
        {state.chatHistory.length === 0 ? (
          <div className="empty-chat">
            <div className="empty-chat-icon"></div>
            <h3> AI </h3>
            <p></p>
            <div className="suggestions">
              <button 
                onClick={() => handleSendMessage('')}
                className="suggestion-btn"
              >
                
              </button>
              <button 
                onClick={() => handleSendMessage('')}
                className="suggestion-btn"
              >
                
              </button>
              <button 
                onClick={() => handleSendMessage('')}
                className="suggestion-btn"
              >
                
              </button>
            </div>
          </div>
        ) : (
          <div className="messages-list">
            {state.chatHistory.map((message) => (
              <ChatBubble
                key={message.id}
                message={message}
                isTyping={message.isTyping}
              />
            ))}
            {isLoading && (
              <div className="typing-indicator">
                <div className="typing-dots">
                  <span></span>
                  <span></span>
                  <span></span>
                </div>
                <span className="typing-text">AI ...</span>
              </div>
            )}
            <div ref={messagesEndRef} />
          </div>
        )}
      </div>

      <div className="chat-input-container">
        <ChatInput
          onSendMessage={handleSendMessage}
          disabled={isLoading}
          placeholder="..."
        />
      </div>
    </div>
  );
};

export default AIAssistant;

